.comment {
  display: flex;
  margin: 10px 0;
  gap: 5px;

  .commentInput {
    width: 80%;
    height: 40px;
    padding-left: 10px;
    border-radius: 6px;
  }

  .commentWriteBtn {
    width: 20%;
    height: 40px;
    padding: 6px 14px;
    border: none;
    border-radius: 6px;
    background-color: #5e3dea;
    color: #fff;
    text-align: center;
    font-weight: bold;
  }
}
