.feedList {
  height: 80vh;
  margin: 20px 0 0;
  padding: 20px;
  overflow-y: auto;

  &::-webkit-scrollbar {
    width: 5px;
  }

  &::-webkit-scrollbar-thumb {
    background-color: #5e3dea;
    border-radius: 6px;
  }

  &::-webkit-scrollbar-track {
    background-color: #f0f0f0;
  }
}
