.DietContainer {
  margin-bottom: 40px;

  .iconTime {
    width: 20px;

    img {
      display: inline-block;
      width: unset;
      vertical-align: bottom;
    }
  }
  .dietBoxWrapper {
    position: relative;
    display: flex;
    justify-content: center;
    gap: 30px;

    .dietBox {
      width: 330px;
      border: 1px solid #ededed;
      border-radius: 12px;
      overflow: hidden;

      .imgBox {
        border-bottom: 1px solid #ededed;
        height: 220px;
        overflow: hidden;
      }
      .dietInfo {
        padding: 20px;

        .infoText {
          margin-bottom: 12px;
        }

        .nutrientName {
          display: inline-block;
          padding: 10px 14px;
          border-radius: 6px;
          background-color: #e8e3f7;
          color: #5e3dea;
        }
        .foodName {
          font-weight: bold;
        }
        .foodCount {
          color: #5e3dea;
          font-weight: bold;

          .gram {
            color: #333;
          }
        }
        .foodInfo {
          width: 100%;
          color: #666;
          font-weight: 300;
          font-size: 16px;
          line-height: 1.4;
          word-break: keep-all;
          margin-bottom: 0;
          overflow: hidden;
          text-overflow: ellipsis;
          display: -webkit-box;
          -webkit-line-clamp: 2;
          -webkit-box-orient: vertical;
        }

        .foodCount {
          .numerator {
            font-size: 14px;
            vertical-align: top;
          }
          .denominator {
            font-size: 14px;
            vertical-align: bottom;
          }
        }
      }
    }
  }
}
