.SignUp {
  padding: 120px 0 120px;
  width: 100%;
  height: auto;

  input:focus {
    outline: 1px solid #5e3dea;
  }

  input::-webkit-inner-spin-button {
    -webkit-appearance: none;
    margin: 0;
  }

  img {
    display: block;
    width: 100%;
  }

  .signUpInner {
    width: 600px;
    padding: 60px;
    border: 1px solid #ededed;
    border-radius: 16px;

    .logo {
      width: 30%;
      margin: 10px 0;
    }

    h1 {
      font-size: 32px;
      font-weight: bold;
      padding-bottom: 16px;
    }
    p {
      padding-bottom: 40px;
      color: #5e3dea;
    }

    .inputWrapper {
      width: 100%;
      display: flex;
      justify-content: center;
      flex-direction: column;

      .nickNameInput {
        position: relative;
        display: flex;
        justify-content: space-between;
        align-items: center;

        button {
          display: block;
          margin: 10px 0 24px 12px;
          width: 20%;
          height: 40px;
          border-radius: 6px;
          border: 1px solid #5e3dea;
          background-color: #fff;
          color: #5e3dea;
          transition: all 0.2s;
        }

        button:hover {
          border: 1px solid #fff;
          background-color: #5e3dea;
          color: #fff;
          transition: all 0.2s;
        }
      }

      input {
        width: 100%;
        height: 40px;
        border-radius: 6px;
        border: 1px solid #ededed;
        margin: 10px 0 24px 0;
        padding: 16px;
        color: #333;
      }

      input::placeholder {
        color: #ccc;
        padding-left: 6px;
        font-size: 16px;
      }

      label {
        text-align: left;
        font-size: 16px;
        color: #666;
      }

      .genderAndageBox {
        position: relative;
        display: flex;
        justify-content: space-between;
        align-items: center;
        margin-bottom: 10px;

        .genderCheck {
          .checkBoxInput {
            width: 20px;
            height: 20px;
          }
          label {
            margin: 0 20px 0 8px;
          }
        }
        .ageInputWrapper {
          display: flex;
          align-items: center;

          input {
            margin: 10px 12px 24px 12px;
          }

          span {
            display: block;
            height: 30px;
            text-align: left;
            font-size: 16px;
            color: #666;
          }
        }
      }
    }

    .btnSignUp {
      display: block;
      margin-top: 20px;
      width: 100%;
      border: none;
      background-color: #5e3dea;
      color: #fff;
      border-radius: 6px;
      padding: 12px 0;
      cursor: pointer;
    }
  }
  .errors {
    position: absolute;
    left: 0;
    bottom: 6px;
    font-size: 12px;
    color: #f05650;
  }

  .inputBox {
    position: relative;
  }

  .fontBold {
    font-weight: bold;
  }
}
